"use client";

import { useEffect, useRef, useState } from "react";

// Interface definitions (match your backend)
enum MessageType {
  MATCHED = "MATCHED",
  FAILURE = "FAILURE"
}

enum RequestType {
  OFFER = "OFFER",
  ANSWER = "ANSWER", 
  ADD_ICE_CANDIDATES = "ADD_ICE_CANDIDATES",
  FIND_NEXT = "FIND_NEXT",
  SKIP = "SKIP",
  STOP = "STOP"
}

enum ErrorType {
  NO_MATCH_FOUND = "NO_MATCH_FOUND",
  INVALID_PAYLOAD = "INVALID_PAYLOAD"
}

export default function VideoChat() {
  const localVideoRef = useRef<HTMLVideoElement | null>(null);
  const remoteVideoRef = useRef<HTMLVideoElement | null>(null);
  const [started, setStarted] = useState(false);
  const [connected, setConnected] = useState(false);
  const [searching, setSearching] = useState(false);
  const peerConnection = useRef<RTCPeerConnection | null>(null);
  const ws = useRef<WebSocket | null>(null);
  const localStream = useRef<MediaStream | null>(null);
  const [connectionState, setConnectionState] = useState<string>('new');
  const [error, setError] = useState<string>('');

  useEffect(() => {
    initializeWebSocket();
    getLocalStream();
    
    return () => {
      cleanup();
    };
  }, []);

  function initializeWebSocket() {
    try {
      const socket = new WebSocket("ws://localhost:8080");
      ws.current = socket;

      socket.onopen = () => {
        console.log("WebSocket connected");
        setError('');
      };

      socket.onmessage = async (e) => {
        try {
          const data = JSON.parse(e.data.toString());
          const type = data.type;
          
          if (!type) {
            console.log("Message without type received");
            return;
          }

          console.log("Received message:", type);

          switch (type) {
            case "SKIP":
              console.log("Partner skipped");
              handlePartnerDisconnect();
              break;
              
            case MessageType.MATCHED:
              console.log("Matched with partner");
              setSearching(false);
              await initializePeerConnection();
              break;
              
            case RequestType.OFFER:
              const offer = data.payload?.offer;
              if (offer) {
                console.log("Offer received");
                await handleOffer(offer);
              }
              break;
              
            case RequestType.ANSWER:
              const answer = data.payload?.answer;
              if (answer) {
                console.log("Answer received");
                await handleAnswer(answer);
              }
              break;
              
            case RequestType.ADD_ICE_CANDIDATES:
              const candidate = data.payload?.sdp;
              if (candidate) {
                console.log("ICE candidate received");
                await handleIceCandidate(candidate);
              }
              break;
              
            case MessageType.FAILURE:
              const errorMsg = data.payload?.error || data.error || 'Unknown error';
              console.log("Server error:", errorMsg);
              setError(errorMsg);
              setSearching(false);
              // Don't disconnect on ICE candidate errors
              if (errorMsg !== ErrorType.INVALID_PAYLOAD) {
                handleError(errorMsg);
              }
              break;
              
            default:
              console.log("Unknown message type:", data);
          }
        } catch (err) {
          console.error("Error parsing WebSocket message:", err);
        }
      };

      socket.onerror = (error) => {
        console.error("WebSocket error:", error);
        setError("Connection error");
        setSearching(false);
      };

      socket.onclose = () => {
        console.log("WebSocket disconnected");
        setError("Disconnected from server");
      };
    } catch (err) {
      console.error("Error initializing WebSocket:", err);
      setError("Failed to connect to server");
    }
  }

  async function getLocalStream() {
    try {
      const stream = await navigator.mediaDevices.getUserMedia({ 
        video: { 
          width: { ideal: 1280 },
          height: { ideal: 720 },
          facingMode: 'user'
        }, 
        audio: {
          echoCancellation: true,
          noiseSuppression: true
        }
      });
      
      localStream.current = stream;
      
      // Wait for video element to be available
      let attempts = 0;
      while (!localVideoRef.current && attempts < 100) {
        await new Promise((resolve) => setTimeout(resolve, 50));
        attempts++;
      }
      
      if (localVideoRef.current) {
        localVideoRef.current.srcObject = stream;
      }
      
      console.log("Local stream acquired");
    } catch (error) {
      console.error("Error accessing media devices:", error);
      setError("Camera/microphone access denied");
    }
  }

  async function initializePeerConnection() {
    try {
      // Clean up existing connection
      if (peerConnection.current) {
        peerConnection.current.close();
      }

      // Create new peer connection
      const pc = new RTCPeerConnection({
        iceServers: [
          { urls: 'stun:stun.l.google.com:19302' }
        ]
      });

      peerConnection.current = pc;

      // Add local stream tracks
      if (localStream.current) {
        localStream.current.getTracks().forEach(track => {
          pc.addTrack(track, localStream.current!);
        });
        console.log("Local tracks added");
      }

      // Set up event handlers
      setupPeerConnectionHandlers(pc);

      console.log("Peer connection initialized");
    } catch (error) {
      console.error("Error initializing peer connection:", error);
      setError("Failed to initialize connection");
    }
  }

  function setupPeerConnectionHandlers(pc: RTCPeerConnection) {
    // Handle remote stream
    pc.ontrack = (event) => {
      console.log("Remote track received:", event.track.kind);
      if (remoteVideoRef.current && event.streams[0]) {
        remoteVideoRef.current.srcObject = event.streams[0];
        setConnected(true);
        setError('');
      }
    };

    // Handle ICE candidates - FIXED: Only send non-null candidates
    pc.onicecandidate = (event) => {
      // Only send actual candidates, not the null "end-of-candidates" signal
      if (event.candidate && ws.current && ws.current.readyState === WebSocket.OPEN) {
        console.log("Sending ICE candidate");
        ws.current.send(JSON.stringify({
          type: RequestType.ADD_ICE_CANDIDATES,
          payload: { 
            sdp: event.candidate,
            id: 1
          }
        }));
      } else if (!event.candidate) {
        console.log("ICE gathering complete (null candidate - not sending)");
      }
    };

    // Handle connection state changes
    pc.onconnectionstatechange = () => {
      const state = pc.connectionState;
      setConnectionState(state);
      console.log("Connection state:", state);
      
      if (state === 'connected') {
        setConnected(true);
        setError('');
      } else if (state === 'disconnected') {
        setConnected(false);
      } else if (state === 'failed') {
        setConnected(false);
        setError('Connection failed');
      } else if (state === 'closed') {
        setConnected(false);
      }
    };

    // Handle negotiation needed (for creating offers)
    pc.onnegotiationneeded = async () => {
      try {
        console.log("Creating offer");
        const offer = await pc.createOffer();
        await pc.setLocalDescription(offer);
        
        if (ws.current && ws.current.readyState === WebSocket.OPEN) {
          ws.current.send(JSON.stringify({
            type: RequestType.OFFER,
            payload: { offer }
          }));
          console.log("Offer sent");
        }
      } catch (error) {
        console.error("Error creating offer:", error);
        setError("Failed to create offer");
      }
    };
  }

  async function handleOffer(offer: RTCSessionDescriptionInit) {
    try {
      if (!peerConnection.current) {
        await initializePeerConnection();
      }

      const pc = peerConnection.current!;
      
      await pc.setRemoteDescription(new RTCSessionDescription(offer));
      console.log("Remote description set");

      const answer = await pc.createAnswer();
      await pc.setLocalDescription(answer);
      
      if (ws.current && ws.current.readyState === WebSocket.OPEN) {
        ws.current.send(JSON.stringify({
          type: RequestType.ANSWER,
          payload: { answer }
        }));
        console.log("Answer sent");
      }
    } catch (error) {
      console.error("Error handling offer:", error);
      setError("Failed to handle offer");
    }
  }

  async function handleAnswer(answer: RTCSessionDescriptionInit) {
    try {
      if (peerConnection.current && peerConnection.current.remoteDescription === null) {
        await peerConnection.current.setRemoteDescription(new RTCSessionDescription(answer));
        console.log("Answer processed");
      } else {
        console.log("Answer received but remote description already set or no PC");
      }
    } catch (error) {
      console.error("Error handling answer:", error);
    }
  }

  async function handleIceCandidate(candidate: RTCIceCandidateInit) {
    try {
      if (peerConnection.current && peerConnection.current.remoteDescription) {
        await peerConnection.current.addIceCandidate(new RTCIceCandidate(candidate));
        console.log("ICE candidate added");
      } else {
        console.log("ICE candidate received before remote description");
      }
    } catch (error) {
      console.error("Error adding ICE candidate:", error);
    }
  }

  function handleNext() {
    if (!ws.current || ws.current.readyState !== WebSocket.OPEN) {
      setError("Not connected to server");
      return;
    }

    if (!started) {
      setStarted(true);
    }

    setSearching(true);
    setError('');
    handlePartnerDisconnect();

    ws.current.send(JSON.stringify({ 
      type: "FIND_NEXT",
      payload: {}
    }));
    console.log("Searching for partner...");
  }

  function handleStop() {
    if (ws.current && ws.current.readyState === WebSocket.OPEN) {
      ws.current.send(JSON.stringify({ 
        type: RequestType.STOP,
        payload: {}
      }));
    }
    
    handlePartnerDisconnect();
    setStarted(false);
    setSearching(false);
    setError('');
  }

  function handlePartnerDisconnect() {
    setConnected(false);
    setConnectionState('new');
    
    if (peerConnection.current) {
      peerConnection.current.close();
      peerConnection.current = null;
    }

    if (remoteVideoRef.current) {
      remoteVideoRef.current.srcObject = null;
    }
  }

  function handleError(error: string) {
    console.error("Handling error:", error);
    handlePartnerDisconnect();
    setSearching(false);
  }

  function cleanup() {
    if (peerConnection.current) {
      peerConnection.current.close();
      peerConnection.current = null;
    }
    
    if (localStream.current) {
      localStream.current.getTracks().forEach(track => track.stop());
      localStream.current = null;
    }
    
    if (ws.current && ws.current.readyState === WebSocket.OPEN) {
      ws.current.close();
    }
  }

  function getStatusText() {
    if (error) return error;
    if (searching) return "Searching for partner...";
    if (connected) return "Connected";
    if (started) return "Ready";
    return "Click START to begin";
  }

  function getStatusColor() {
    if (error) return "text-red-600";
    if (searching) return "text-yellow-600";
    if (connected) return "text-green-600";
    return "text-gray-600";
  }

  return (
    <div className="relative min-h-screen w-screen bg-gray-900 flex flex-col">
      {/* Video Container */}
      <div className="flex-1 flex justify-center items-center p-4 gap-4">
        {/* Remote video */}
        <div className="relative w-1/2 h-full max-h-96 bg-gray-800 rounded-xl overflow-hidden">
          <video 
            ref={remoteVideoRef} 
            autoPlay 
            playsInline
            className="w-full h-full object-cover"
          />
          {!connected && (
            <div className="absolute inset-0 flex items-center justify-center">
              <div className="text-center text-white">
                <div className="text-lg mb-2">Partner Video</div>
                {searching && (
                  <div className="flex items-center justify-center">
                    <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-white"></div>
                  </div>
                )}
              </div>
            </div>
          )}
        </div>
        
        {/* Local video */}
        <div className="relative w-1/2 h-full max-h-96 bg-gray-800 rounded-xl overflow-hidden">
          <video 
            ref={localVideoRef} 
            muted 
            autoPlay 
            playsInline
            className="w-full h-full object-cover scale-x-[-1]"
          />
          <div className="absolute top-2 left-2 bg-black bg-opacity-50 text-white px-2 py-1 rounded text-sm">
            You
          </div>
        </div>
      </div>
      
      {/* Controls */}
      <div className="bg-gray-800 p-4 flex items-center justify-between">
        <div className="flex gap-4">
          <button 
            className={`px-6 py-3 rounded-lg font-semibold transition-all ${
              searching 
                ? 'bg-gray-600 cursor-not-allowed' 
                : 'bg-blue-600 hover:bg-blue-700 active:scale-95'
            } text-white`}
            onClick={handleNext}
            disabled={searching}
          >
            {searching ? "Searching..." : (started ? "NEXT" : "START")}
          </button>
          
          <button 
            className="px-6 py-3 rounded-lg bg-red-600 hover:bg-red-700 text-white font-semibold transition-all active:scale-95"
            onClick={handleStop}
          >
            STOP
          </button>
        </div>
        
        <div className="text-right">
          <div className={`text-sm font-medium ${getStatusColor()}`}>
            {getStatusText()}
          </div>
          <div className="text-xs text-gray-400 mt-1">
            State: {connectionState}
          </div>
        </div>
      </div>
    </div>
  );
}